// API Configuration for Event Management System
// Update this file when deploying to different environments

// Environment detection
const isProduction = import.meta.env.PROD;
const isDevelopment = import.meta.env.DEV;

// API Base URLs for different environments
const API_CONFIGS = {
  // Youware platform (default)
  youware: {
    base: '',
    name: 'Same-Origin Backend'
  },

  youwareLegacy: {
    base: 'https://backend.youware.com',
    name: 'Youware Backend (Legacy)'
  },
  
  // Hostinger VPS deployment
  hostinger: {
    base: 'https://your-domain.com', // Replace with your actual domain
    name: 'Hostinger VPS'
  },
  
  // Local development
  local: {
    base: 'http://localhost:3000',
    name: 'Local Development'
  }
};

// ⚠️ DEPLOYMENT CONFIGURATION ⚠️
// Change this when deploying to different platforms:
// - 'youware' for Youware platform
// - 'hostinger' for Hostinger VPS
// - 'local' for local development
const CURRENT_ENVIRONMENT = 'youware'; // 👈 CHANGE THIS FOR DEPLOYMENT

// Override base URL with runtime env if provided
const runtimeBase = (typeof window !== 'undefined' && (window as any)?.ywConfig?.apiBase) ||
  (typeof window !== 'undefined' && (window as any)?.ywConfig?.backend?.baseUrl);

if (runtimeBase) {
  API_CONFIGS.runtime = {
    base: runtimeBase,
    name: 'Runtime Config'
  } as const;
}

// Allow query param override (?apiBase=...)
const urlParams = typeof window !== 'undefined' ? new URLSearchParams(window.location.search) : null;
const queryApiBase = urlParams?.get('apiBase');

if (queryApiBase) {
  API_CONFIGS.query = {
    base: queryApiBase,
    name: 'Query Override'
  } as const;
}

// Get current API configuration
const getCurrentApiConfig = () => {
  const configOrder: Array<keyof typeof API_CONFIGS> = ['query', 'runtime', CURRENT_ENVIRONMENT as keyof typeof API_CONFIGS];

  for (const key of configOrder) {
    const config = API_CONFIGS[key];
    if (config) {
      return config;
    }
  }

  console.warn(`⚠️ Unknown API environment: ${CURRENT_ENVIRONMENT}. Falling back to same-origin API.`);
  return API_CONFIGS.youware;
};

const resolveBaseUrl = (base: string) => {
  if (base) {
    return base;
  }

  return '';
};

// Export API configuration
const rawConfig = getCurrentApiConfig();
const resolvedBase = resolveBaseUrl(rawConfig.base);
export const API_CONFIG = {
  ...rawConfig,
  base: resolvedBase,
} as const;

console.log(`🔧 API Environment: ${API_CONFIG.name} (${API_CONFIG.base || 'same-origin'})`);

// API endpoints
export const makeEndpoint = (path: string) => {
  if (!API_CONFIG.base) {
    return path;
  }
  return `${API_CONFIG.base}${path}`;
};

export const API_ENDPOINTS = {
  events: makeEndpoint('/api/events'),
  eventsById: (id: string) => makeEndpoint(`/api/events/${id}`),
  eventsBulk: makeEndpoint('/api/events/bulk'),
  eventsExport: makeEndpoint('/api/events/export'),
  authLogin: makeEndpoint('/api/auth/login'),
  authLogout: makeEndpoint('/api/auth/logout'),
  authMe: makeEndpoint('/api/auth/me'),
  users: makeEndpoint('/api/users'),
  usersById: (id: string) => makeEndpoint(`/api/users/${id}`),
  licenses: makeEndpoint('/api/licenses'),
  licensesById: (id: string) => makeEndpoint(`/api/licenses/${id}`),
  licensesCheck: makeEndpoint('/api/licenses/check'),
  licensesGenerate: makeEndpoint('/api/licenses/generate'),
  licensesPreviewSerial: makeEndpoint('/api/licenses/preview-serial'),
} as const;

// Environment detection utilities
export const isScreenshotEnvironment = () => {
  // Only detect actual screenshot/headless environments, not production domains
  return navigator.userAgent.includes('HeadlessChrome') ||
         navigator.userAgent.includes('PhantomJS') ||
         (typeof window !== 'undefined' && window.navigator.webdriver === true);
};

type PrimitiveHeaderValue = string | number | boolean;

const YW_HEADER_CANONICAL_MAP: Record<string, string> = {
  'x-project-id': 'X-Project-Id',
  'x-encrypted-yw-id': 'X-Encrypted-Yw-ID',
  'x-is-login': 'X-Is-Login',
  'x-yw-env': 'X-Yw-Env',
};

const isPrimitiveHeaderValue = (value: unknown): value is PrimitiveHeaderValue =>
  typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean';

const isPlainRecord = (value: unknown): value is Record<string, unknown> =>
  !!value && typeof value === 'object' && !Array.isArray(value);

const extractFirstPrimitive = (input: unknown): PrimitiveHeaderValue | undefined => {
  if (isPrimitiveHeaderValue(input)) {
    return input;
  }

  if (Array.isArray(input)) {
    for (const item of input) {
      const value = extractFirstPrimitive(item);
      if (value !== undefined) {
        return value;
      }
    }

    return undefined;
  }

  if (isPlainRecord(input)) {
    for (const value of Object.values(input)) {
      const primitive = extractFirstPrimitive(value);
      if (primitive !== undefined) {
        return primitive;
      }
    }
  }

  return undefined;
};

const getChildValue = (container: unknown, key: string): unknown => {
  if (!isPlainRecord(container)) {
    return undefined;
  }

  if (key in container) {
    return container[key];
  }

  const lowerKey = key.toLowerCase();
  const matchedKey = Object.keys(container).find(
    (candidate) => candidate.toLowerCase() === lowerKey
  );

  return matchedKey ? container[matchedKey] : undefined;
};

const getValueFromPaths = (source: unknown, paths: string[][]): PrimitiveHeaderValue | undefined => {
  for (const path of paths) {
    let current: unknown = source;
    let matched = true;

    for (const segment of path) {
      current = getChildValue(current, segment);
      if (current === undefined) {
        matched = false;
        break;
      }
    }

    if (!matched) {
      continue;
    }

    const primitive = extractFirstPrimitive(current);
    if (primitive !== undefined) {
      return primitive;
    }
  }

  return undefined;
};

const findValueBySnippets = (source: unknown, snippets: string[]): PrimitiveHeaderValue | undefined => {
  if (!source || (typeof source !== 'object' && !Array.isArray(source))) {
    return undefined;
  }

  const visited = new Set<unknown>();
  const stack: unknown[] = [source];

  while (stack.length > 0) {
    const current = stack.pop();
    if (!current || typeof current !== 'object') {
      continue;
    }

    if (visited.has(current)) {
      continue;
    }

    visited.add(current);

    if (Array.isArray(current)) {
      for (const item of current) {
        stack.push(item);
      }
      continue;
    }

    for (const [key, value] of Object.entries(current)) {
      const keyLc = key.toLowerCase();
      if (snippets.every((snippet) => keyLc.includes(snippet))) {
        const primitive = extractFirstPrimitive(value);
        if (primitive !== undefined) {
          return primitive;
        }
      }

      if (value && typeof value === 'object') {
        stack.push(value);
      }
    }
  }

  return undefined;
};

const sanitizeHeadersInput = (input: unknown): Record<string, string> => {
  if (!isPlainRecord(input)) {
    return {};
  }

  const result: Record<string, string> = {};

  for (const [rawName, rawValue] of Object.entries(input)) {
    const canonicalName = YW_HEADER_CANONICAL_MAP[rawName.toLowerCase()];
    if (!canonicalName) {
      continue;
    }

    const primitive = extractFirstPrimitive(rawValue);
    if (primitive === undefined) {
      continue;
    }

    result[canonicalName] = String(primitive);
  }

  return result;
};

const getYouwareSdkHeaders = (): Record<string, string> => {
  const sdk = (globalThis as any)?.ywSdk;
  if (!sdk) {
    return {};
  }

  const candidates = [
    () => sdk.getHeaders?.(),
    () => sdk.getRequestHeaders?.(),
    () => sdk.headers,
    () => sdk.getAuthHeaders?.(),
  ];

  for (const candidate of candidates) {
    try {
      const value = candidate?.();
      if (!value || value instanceof Promise) {
        continue;
      }

      const sanitized = sanitizeHeadersInput(value);
      if (Object.keys(sanitized).length > 0) {
        return sanitized;
      }
    } catch {
      // Ignore SDK header lookup errors to avoid breaking fetch calls.
    }
  }

  return {};
};

const getYouwareConfigHeaders = (): Record<string, string> => {
  const visited = new Set<any>();
  const tryConfigs: any[] = [];

  const enqueue = (candidate: any) => {
    if (!candidate || visited.has(candidate)) {
      return;
    }
    visited.add(candidate);
    tryConfigs.push(candidate);
  };

  const safeRead = <T>(reader: () => T): T | undefined => {
    try {
      return reader();
    } catch (error) {
      if (import.meta.env.DEV) {
        console.debug('Youware config access blocked:', error);
      }
      return undefined;
    }
  };

  enqueue(safeRead(() => (globalThis as any)?.ywConfig));
  enqueue(safeRead(() => (globalThis as any)?.yw?.config));
  enqueue(safeRead(() => (globalThis as any)?.__ywConfig));

  const parentWindow = (() => {
    try {
      if (typeof window !== 'undefined' && window.parent && window.parent !== window) {
        return window.parent;
      }
    } catch (error) {
      if (import.meta.env.DEV) {
        console.debug('Unable to reference parent window for Youware config:', error);
      }
    }
    return null;
  })();

  if (parentWindow) {
    enqueue(safeRead(() => (parentWindow as any).ywConfig));
    enqueue(safeRead(() => (parentWindow as any).yw?.config));
    enqueue(safeRead(() => (parentWindow as any).__ywConfig));
  }

  let config: any = null;
  while (tryConfigs.length > 0 && !config) {
    const candidate = tryConfigs.shift();
    if (candidate && typeof candidate === 'object') {
      config = candidate;
    }
  }

  if (!config) {
    return {};
  }

  const result: Record<string, string> = {};
  const setHeader = (name: string, value: PrimitiveHeaderValue | undefined) => {
    if (value === undefined) {
      return;
    }
    result[name] = String(value);
  };

  const encryptedPaths = [
    ['encrypted_yw_id'],
    ['encryptedYwId'],
    ['owner_yw_id'],
    ['ownerYwId'],
    ['project', 'encrypted_yw_id'],
    ['project', 'encryptedYwId'],
    ['project', 'owner_yw_id'],
    ['project', 'ownerYwId'],
    ['workspace', 'owner_yw_id'],
    ['workspace', 'ownerYwId'],
  ];

  const projectIdPaths = [
    ['project_id'],
    ['projectId'],
    ['project', 'id'],
    ['project', 'project_id'],
    ['project', 'projectId'],
    ['workspace', 'project_id'],
    ['workspace', 'projectId'],
    ['workspace', 'project', 'id'],
  ];

  const isLoginPaths = [
    ['is_login'],
    ['isLogin'],
    ['auth', 'is_login'],
    ['auth', 'isLogin'],
    ['user', 'is_login'],
    ['user', 'isLogin'],
    ['session', 'is_login'],
    ['session', 'isLogin'],
  ];

  const envPaths = [
    ['yw_env'],
    ['ywEnv'],
    ['env'],
    ['environment'],
    ['project', 'env'],
    ['project', 'environment'],
    ['workspace', 'env'],
    ['workspace', 'environment'],
  ];

  setHeader(
    'X-Encrypted-Yw-ID',
    getValueFromPaths(config, encryptedPaths) ??
      findValueBySnippets(config, ['encrypted', 'yw']) ??
      findValueBySnippets(config, ['owner', 'yw'])
  );

  setHeader(
    'X-Project-Id',
    getValueFromPaths(config, projectIdPaths) ??
      findValueBySnippets(config, ['project', 'id'])
  );

  setHeader(
    'X-Is-Login',
    getValueFromPaths(config, isLoginPaths) ??
      findValueBySnippets(config, ['is', 'login']) ??
      findValueBySnippets(config, ['logged', 'in'])
  );

  setHeader(
    'X-Yw-Env',
    getValueFromPaths(config, envPaths) ??
      findValueBySnippets(config, ['yw', 'env']) ??
      findValueBySnippets(config, ['environment'])
  );

  return result;
};

const getYouwareHeaders = (): Record<string, string> => {
  const headers = getYouwareConfigHeaders();
  const sdkHeaders = getYouwareSdkHeaders();

  for (const [name, value] of Object.entries(sdkHeaders)) {
    if (!headers[name]) {
      headers[name] = value;
    }
  }

  return headers;
};

const normalizeHeaderValue = (value: string): string | undefined => {
  const trimmed = value.trim();
  if (!trimmed || trimmed === 'undefined' || trimmed === 'null') {
    return undefined;
  }

  return trimmed;
};

let authTokenGetter: (() => string | null) | null = null;

export const setAuthTokenGetter = (getter: () => string | null) => {
  authTokenGetter = getter;
};

const buildHeaders = (existing: HeadersInit | undefined): Headers => {
  const headers = new Headers(existing);
  const token = authTokenGetter ? authTokenGetter() : null;
  if (token) {
    headers.set('Authorization', `Bearer ${token}`);
  }

  const youwareHeaders = getYouwareHeaders();
  for (const [name, rawValue] of Object.entries(youwareHeaders)) {
    if (name.toLowerCase() === 'authorization') {
      continue;
    }

    const sanitizedValue = normalizeHeaderValue(String(rawValue));
    if (!sanitizedValue) {
      continue;
    }

    const existingValue = headers.get(name);
    if (existingValue) {
      const normalizedExisting = normalizeHeaderValue(existingValue);
      if (normalizedExisting) {
        continue;
      }
    }

    headers.set(name, sanitizedValue);
  }

  return headers;
};

// API call wrapper with retry logic
export const apiCall = async (url: string, options: RequestInit = {}, maxRetries = 3): Promise<Response> => {
  let lastError: Error | null = null;
  
  for (let attempt = 1; attempt <= maxRetries; attempt++) {
    try {
      console.log(`🔄 API call attempt ${attempt}/${maxRetries} to:`, url);
      
      const headers = buildHeaders(options.headers);

      const response = await fetch(url, {
        ...options,
        headers,
        signal: AbortSignal.timeout(10000), // 10 second timeout
      });
      
      console.log(`📡 Response received (attempt ${attempt}):`, {
        status: response.status,
        statusText: response.statusText,
        url: response.url,
        ok: response.ok
      });
      
      return response;
    } catch (error) {
      lastError = error instanceof Error ? error : new Error('Unknown error');
      
      console.warn(`⚠️ API call failed (attempt ${attempt}/${maxRetries}):`, {
        targetUrl: url,
        error: lastError.message,
        willRetry: attempt < maxRetries
      });
      
      if (attempt < maxRetries) {
        const delay = Math.pow(2, attempt - 1) * 1000;
        console.log(`⏱️ Waiting ${delay}ms before retry...`);
        await new Promise(resolve => setTimeout(resolve, delay));
      }
    }
  }
  
  throw new Error(`API call failed after ${maxRetries} attempts: ${lastError?.message || 'Unknown error'}`);
};